import React, { useState, useEffect, useMemo } from 'react';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import Overlay from '@avtopro/overlay';
import Preloader from '@avtopro/preloader';
import ModalHead from './ModalHead';
import ModalClose from './ModalClose';
import './style/modwin.less';

/**
 * Базовое модальное окно.
 */
export default function Modal({
    id,
    mode,
    size,
    children,
    paddingless,
    blockName,
    className,
    disabled,
    hidden,
    onClose,
    onScroll,
    closeOnClick
}) {
    const sizeValue = useMemo(
        () => ({
            custom: typeof size == 'number' && size >= 0 ? size : null,
            preset: typeof size == 'string' ? size : null
        }),
        [size]
    );
    const styleProp = useMemo(
        () => (sizeValue.custom ? { width: sizeValue.custom } : null),
        [size]
    );

    // Модальное окно будет в полноэкранном режиме, если size="fullscreen",
    // но оно может и самостоятельно переходить в полноэкранный режим,
    // когда задано числовое значение size и оно больше размера окна.
    const [forceFullscreen, setFullscreen] = useState(
        sizeValue.preset == 'fullscreen'
    );

    useEffect(() => {
        if (sizeValue.custom == null) return undefined;
        try {
            const mql = window.matchMedia(`(max-width: ${sizeValue.custom}px)`);
            const mqlListener = (e) => {
                setFullscreen(e.matches);
            };

            setFullscreen(mql.matches);

            if (mql.addEventListener) {
                mql.addEventListener('change', mqlListener);
                return () => {
                    mql.removeEventListener('change', mqlListener);
                };
            }
            // Методы addListener и removeListener устарели,
            // но применяются для совместимости с Safari <= 12.
            if (mql.addListener) {
                mql.addListener(mqlListener);
                return () => {
                    mql.removeListener(mqlListener);
                };
            }

            throw new Error('MediaQueryList is not supported.');
        } catch (e) {
            // Если MediaQueryList не поддерживается (только устаревшие браузеры),
            // лучше показать модал сразу в полноэкранном режиме, избегая прокрутки.
            setFullscreen(true);
            return undefined;
        }
    }, [size]);

    return (
        <Overlay
            hidden={hidden}
            onClose={onClose}
            onScroll={onScroll}
            closeOnClick={closeOnClick}
        >
            <div
                id={id}
                style={styleProp}
                className={classNames({
                    [className]: true,
                    [blockName]: true,
                    [`${blockName}--${sizeValue.preset}`]:
                        sizeValue.preset && !forceFullscreen,
                    [`${blockName}--fullscreen`]: forceFullscreen,
                    [`${blockName}--no-pad`]: paddingless
                })}
            >
                {!disabled ? null : <Preloader fixed />}
                <div className={`${blockName}__content`}>
                    <ModalHead mode={mode} />
                    {children}
                </div>
                {onClose && !disabled ? <ModalClose onClick={onClose} /> : null}
            </div>
        </Overlay>
    );
}

Modal.propTypes = {
    /**
     * Идентификатор модального окна.
     */
    id: PropTypes.string,
    /**
     * Режим для стилизации модала под информационное окно.
     */
    mode: PropTypes.oneOf(['info', 'warning', 'error', 'success']),
    /**
     * Ширина модального окна: одна из стандартных, или кастомизированная.
     */
    size: PropTypes.oneOfType([
        PropTypes.number,
        PropTypes.oneOf(['wide', 'wider', 'fullscreen'])
    ]),
    /**
     * У окна нет горизонтальных внутренних отступов.
     */
    paddingless: PropTypes.bool,
    /**
     * Базовый CSS-класс.
     */
    blockName: PropTypes.string,
    /**
     * Дополнительный CSS-класс для кастомизации.
     */
    className: PropTypes.string,
    /**
     * Заблокировать окно, крутить прелоадер.
     */
    disabled: PropTypes.bool,
    /**
     * Скрытое модальное окно удаляется из документа,
     * но сохраняет своё внутреннее состояние.
     */
    hidden: PropTypes.bool,
    /**
     * Обработчик закрытия модала, будет вызван после нажатия Esc, или кнопки-крестика.
     * @TODO скрывать окно также по клику на Overlay.
     */
    onClose: PropTypes.func,
    /**
     * Обработчик прокрутки контейнера модального окна.
     */
    onScroll: PropTypes.func,
    /**
     * Содержимое модального окна.
     */
    children: PropTypes.node,
    /**
     * Вызывать onClose по клику на фон модального окна.
     */
    closeOnClick: PropTypes.bool
};

Modal.defaultProps = {
    id: null,
    mode: null,
    size: null,
    paddingless: false,
    blockName: 'modwin',
    className: '',
    disabled: false,
    hidden: false,
    onClose: null,
    onScroll: null,
    children: null,
    closeOnClick: false
};
