/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/no-static-element-interactions */
import React from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import './style/index.less';

/**
 * Перекрывающий блок с полупрозрачным фоном
 * для построения всевозможных всплывающих элементов (модалов, прелоадеров).
 * Предусмотрена возможность открытия нескольких Overlay одновременно,
 * что с точки зрения интерфейса может считаться некорректной ситуацией,
 * но на практике встречается (когда поверх одного модала всплывает другой).
 * В таком случае будет показан только "верхний" (последний созданный) Overlay,
 * а остальные будут скрыты. Но следующий за "верхним" автоматически отобразится
 * как только тот будет закрыт и т. д.
 */
export default class Overlay extends React.Component {
    /**
     * Создаёт контейнер для React-компонента Overlay.
     * @param {Object} [props] - свойства DOM-элемента контейнера.
     * @returns {HTMLElement}
     */
    static createContainer(props) {
        if (globalThis.document === undefined) {
            return null;
        }

        const container = document.createElement('div');

        if (props)
            Object.keys(props).forEach((prop) => {
                container[prop] = props[prop];
            });

        Overlay.containers.add(container);

        return container;
    }

    /**
     * Удаляет контейнер из DOM и из коллекции контейнеров Overlay.
     * @param {HTMLElement} container
     */
    static deleteContainer(container) {
        if (container && Overlay.containers.has(container)) {
            Overlay.renderContainer(container, { hidden: true });
            Overlay.containers.delete(container);
        }
    }

    /**
     * Обновляет свойства указанного контейнера и настраивает отображение
     * всей коллекции контейнеров, выбирая из неё один, который будет виден в данный момент.
     * Этот метод должен вызываться всегда, когда происходит перерисовка React-компонента.
     * @param {HTMLElement} container
     * @param {Object} [props]
     */
    static renderContainer(container, props) {
        if (!container || globalThis.document === undefined) {
            return;
        }
        if (props)
            Object.keys(props).forEach((prop) => {
                container[prop] = props[prop];
            });

        const activeContainers = [...Overlay.containers]
            // Удаляем невидимые контейнеры из DOM (React-компонент при этом не затрагивается)
            .map((c) => {
                if (c.parentElement && c.hidden) {
                    c.parentElement.removeChild(c);
                }
                return !c.hidden ? c : null;
            })
            // Далее работаем только с видимыми контейнерами
            .filter((c) => c)
            // И добиваемся того, чтобы в один момент времени был показан только один
            // "верхний" (последний созданный) Overlay, а остальные были полностью скрыты
            .map((c, i, all) => {
                const isLast = i == all.length - 1;
                const isMounted = c.parentElement != null;

                // Из DOM удаляются все контейнеры...
                if (isMounted && !isLast) {
                    return c.parentElement.removeChild(c);
                }
                // ... кроме последнего созданного
                if (!isMounted && isLast) {
                    return document.body.appendChild(c);
                }
                return c;
            })
            // Дополнительная фильтрация контейнеров,
            // чтобы отбросить скрытые и пустые - их нельзя считать "активными"
            .filter((c) => c.parentElement != null && c.childElementCount > 0);

        // Если в документе есть хотя бы один Overlay,
        // нужно отключить прокрутку контента в body
        if (activeContainers.length > 0) {
            document.body.classList.add('overlay-container');
        } else {
            document.body.classList.remove('overlay-container');
        }
    }

    static propTypes = {
        /**
         * Обработчик закрытия, который должен удалить элемент из документа.
         * Если обработчика нет, ничего не произойдёт, модал останется открытым.
         */
        onClose: PropTypes.func,
        /**
         * Обработчик прокрутки.
         */
        onScroll: PropTypes.func,
        /**
         * Вызывать onClose по клику на фон модального окна.
         */
        closeOnClick: PropTypes.bool,
        /**
         * Дополнительный CSS-класс для кастомизации.
         */
        className: PropTypes.string,
        /**
         * Скрыть компонент, не удаляя его из DOM.
         */
        hidden: PropTypes.bool,
        /**
         * Контент модального окна.
         */
        children: PropTypes.any.isRequired
    };

    static defaultProps = {
        onClose: null,
        onScroll: null,
        closeOnClick: false,
        className: '',
        hidden: false
    };

    constructor(props) {
        super(props);
        this.container = Overlay.createContainer({ hidden: props.hidden });
    }

    componentDidMount() {
        document.addEventListener('keydown', this.onEscKeyDown);
        Overlay.renderContainer(this.container);
    }

    componentDidUpdate({ hidden: wasHidden }) {
        const { hidden: isHidden } = this.props;
        if (isHidden != wasHidden) {
            Overlay.renderContainer(this.container, { hidden: isHidden });
        }
    }

    componentWillUnmount() {
        document.removeEventListener('keydown', this.onEscKeyDown);
        Overlay.deleteContainer(this.container);
    }

    static blockName = 'overlay';

    /**
     * Коллекция контейнеров Overlay.
     * Порядок вставки контейнеров важен! Самый последний перекрывает собой предыдущие.
     */
    static containers = new Set();

    /**
     * Вызывает внешний обработчик закрытия.
     */
    close = () => {
        if (this.props.onClose) {
            this.props.onClose();
        }
    };

    /**
     * Обрабатывает прямой клик по overlay
     * и вызывает закрытие модала, если свойство closeOnClick == true.
     */
    onClick = (e) => {
        const { blockName } = this.constructor;
        if (
            e.target.classList.contains(blockName) &&
            this.props.closeOnClick &&
            this.container.contains(e.target)
        ) {
            this.close();
        }
    };

    /**
     * Обрабатывает нажатие на клавишу Esc, которое должно закрывать модал.
     * Всплытие события при необходимости может быть остановлено вложенными компонентами.
     */
    onEscKeyDown = (e) => {
        if (e.keyCode == 27) {
            this.close();
        }
    };

    render() {
        const { hidden, className, onScroll, children } = this.props;
        const { blockName } = this.constructor;

        const overlay = (
            <div
                className={classNames(className, blockName, {
                    [`${blockName}--hidden`]: hidden
                })}
                onClick={this.onClick}
                onScroll={onScroll}
            >
                {children}
            </div>
        );

        // В браузере через DOM API создаётся отдельный контейнер
        // для встраивания Overlay в документ через портал,
        // что позволяет избежать проблем с ограниченной через overflow: hidden
        // областью видимости, и с наложением нескольких Overlay друг на друга.
        // При SSR это недоступно, компонент встраивается по месту вызова.
        return this.container
            ? ReactDOM.createPortal(overlay, this.container)
            : overlay;
    }
}
