import React, { useRef, useImperativeHandle } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import memoize from 'memoize-one';
import cyrb53 from '@avtopro/cyrb53';

/**
 * Компонент для вывода списка опций
 * Применяется как составная часть Select и List
 */
export default class DataList extends React.Component {
    static propTypes = {
        /**
         * Базовый CSS-класс компонента
         */
        blockName: PropTypes.string,
        blockSize: PropTypes.oneOf(['', 'sm']),
        /**
         * Массив React-элементов, которые выводятся как элементы списка
         * У каждого элемента должно быть свойство value для сравнения с selectedValues
         */
        options: PropTypes.arrayOf(PropTypes.object).isRequired,
        /**
         * Объект Set с коллекцией выбранных значений
         */
        selectedValues: PropTypes.object.isRequired,
        /**
         * Скрывать ли выбранные опции из списка
         * Эта опция может давать странные эффекты при навигации стрелочками, используйте разумно
         */
        hideSelectedValues: PropTypes.bool,
        /**
         * Показывать названия групп опций
         * Если false, то опции всё равно разделяются на отдельные списки
         */
        showGroupNames: PropTypes.bool,
        /**
         * Если компонент находится в фокусе, он отслеживает нажатия "горячих клавиш"
         */
        isFocused: PropTypes.bool,
        /**
         * Обработчик выбора опции
         */
        onChange: PropTypes.func,
        /**
         * Включить множественный выбор опций
         * Теперь множественный выбор выглядит как чекбокс
         * Свойство нужно, чтобы не добавлять класс отображение одной выбраной опции
         */
        multiple: PropTypes.bool,
        /**
         * Функция обработки содержимого опции с целью визуального выделения
         * тех частей текста опции, которые совпадают с поисковым словом (query)
         */
        emphasizeOption: PropTypes.func,
        /**
         * Количество опций, которые будут одновременно видны в выпадающем списке,
         * независимо от высоты самих опций
         */
        visibleOptionsCount: PropTypes.number
    };

    static defaultProps = {
        blockName: 'pro-data-list',
        blockSize: '',
        hideSelectedValues: false,
        showGroupNames: false,
        emphasizeOption: undefined,
        visibleOptionsCount: 6
    };

    componentDidMount() {
        document.addEventListener('keydown', this.onKeyDown);
    }

    componentWillUnmount() {
        document.removeEventListener('keydown', this.onKeyDown);
    }

    liRefs = {
        selectedLi: null,
        bottomLi: null,
        topLi: null,
        firstLi: null
    };

    /**
     * Группирует опции по значению свойства group
     * @param {Object[]} options
     * @returns {Map} - ключ = группа, значение = массив опций в группе
     */
    groupOptions = memoize((options) => {
        const groups = new Set(options.map((o) => o.props.group));

        return [...groups].reduce(
            (map, group) =>
                map.set(
                    group,
                    options
                        .filter((o) => o.props.group == group)
                        .map((option) => ({
                            key: cyrb53(
                                `${option.props.value}_${option.props.label}`
                            ).toString(16),
                            option
                        }))
                ),
            new Map()
        );
    });

    /**
     * Возвращает позицию первой выбранной опции с начала списка
     * @param {boolean} [last] - с конца списка
     */
    getSelectedPosition(last) {
        const selectedValues = this.props.selectedValues;
        const selectedOptions = this.props.options.filter((option) =>
            selectedValues.has(option.props.value)
        );
        return this.props.options.indexOf(
            selectedOptions[last ? selectedOptions.length - 1 : 0]
        );
    }

    /**
     * Обрабатывает выбор опции
     * @param {*} option - выбранная опция
     * @param {boolean} [single=false] - должна быть выбрана только указанная опция, информация для родительского компонента
     */
    dispatchChangeEvent(option, single = false) {
        if (this.props.onChange) {
            this.props.onChange(option, single);
        }
    }

    /**
     * Обрабатывает нажатие на опцию
     */
    onClick = (e, option) => {
        // e.preventDefault(); <-- без необходимости не применять,
        // иметь в виду, что предотвращение стандартного поведения
        // препятствует выставлению галочки ✔ в чекбоксе опции
        // множественного селекта. Могут быть и другие проблемы
        this.dispatchChangeEvent(option);
    };

    /**
     * Обрабатывает выбор опции по позиции (стрелками с клавиатуры)
     * При таком выборе все ранее выбранные опции сбрасываются, остаётся одна
     * @param {number} i
     */
    setPosition(i) {
        const { options, multiple } = this.props;
        const option = options[i];

        // При множественном выборе управлять опциями при помощи клавиш
        // вверх-вниз будет некорректно, т. к. это приводит к сбросу
        // всех ранее выбраннх опций, такое поведение нельзя понять интуитивно
        // В лучшем случае, клавиши должны смещать фокус опций (как Tab),
        // но никак не приводить к изменению выбора
        if (option && !multiple) {
            this.dispatchChangeEvent(option, true);
        }
    }

    onKeyDown = (e) => {
        if (this.props.isFocused) {
            switch (e.keyCode) {
                case 38 /* Arrow up */:
                    e.preventDefault();
                    this.setPosition(
                        Math.max(0, this.getSelectedPosition() - 1)
                    );
                    break;

                case 40 /* Arrow down */:
                    e.preventDefault();
                    this.setPosition(
                        Math.min(
                            this.props.options.length - 1,
                            this.getSelectedPosition(/* last = */ true) + 1
                        )
                    );
                    break;
            }
        }
    };

    /**
     * Генерирует ref-функцию, которая при вызове принимает ссылку на DOM-элемент списка (li)
     * и записывает её в соответствующее позиции поле объекта liRefs
     * @returns {function: void}
     */
    setLiRef = (selectedIndex) => (liRef) => {
        if (liRef?.li == null) {
            return;
        }
        const { li, index } = liRef;
        const { options, visibleOptionsCount } = this.props;
        // Номер опции, на которой закончится видимая область списка
        const liBottomIndex = Math.max(
            // Список заканчивается либо на фиксированном значении visibleOptionsCount,
            // либо на последней опции в списке, если их кол-во < visibleOptionsCount
            Math.min(options.length, visibleOptionsCount) - 1,
            // Либо на последней (по номеру) выбранной опции, если он > visibleOptionsCount,
            // т. к. мы хотим видеть выбранную опцию в выпадающем списке, даже если она
            // расположена где-то далеко внизу
            selectedIndex
        );
        // Номер первой опции в видимой области отсчитывается от liBottomIndex вверх
        // до нуля, либо уменьшаясь на visibleOptionsCount, смотря что даёт больший номер
        const liTopIndex = Math.max(
            0,
            liBottomIndex - (visibleOptionsCount - 1)
        );

        // Один и тот же элемент списка может одновременно оказаться
        // и первой опцией, и последней, и выбранной, поэтому одна ссылка записывается
        // в разные поля объекта liRefs
        if (index == 0) {
            this.liRefs.firstLi = li;
        }
        if (index == selectedIndex) {
            this.liRefs.selectedLi = li;
        }
        if (index == liBottomIndex) {
            this.liRefs.bottomLi = li;
        }
        if (index == liTopIndex) {
            this.liRefs.topLi = li;
        }
    };

    render() {
        const {
            options,
            blockName,
            blockSize,
            selectedValues,
            hideSelectedValues,
            showGroupNames,
            multiple,
            emphasizeOption
        } = this.props;

        const groups = this.groupOptions(options);
        const output = [];
        const setLiRef = this.setLiRef(this.getSelectedPosition(true));

        groups.forEach((optionsInGroup, group) => {
            // group может быть undefined, если опциям вообще не было передано свойство group
            // Нужно учитывать, что key=undefined игнорируется, поэтому необходимо значение по умолчанию
            output.push(
                <ul
                    key={group || `dl_group_${output.length}`}
                    data-group={showGroupNames ? group : null}
                    className={classNames(blockName, {
                        [`${blockName}--${blockSize}`]: blockSize
                    })}
                >
                    {optionsInGroup.map(({ key, option }) => {
                        const { label, value } = option.props;
                        // Позиция опции в общем списке, не разделённом на группы
                        const index = options.indexOf(option);
                        const selected = selectedValues.has(value);
                        return (
                            <OptionWrapper
                                ref={setLiRef}
                                key={key}
                                index={index}
                                value={value}
                                label={label}
                                blockName={blockName}
                                emphasize={emphasizeOption}
                                selected={selected}
                                highlightSelected={!multiple}
                                hidden={selected && hideSelectedValues}
                                onClick={(e) => this.onClick(e, option)}
                            >
                                {option}
                            </OptionWrapper>
                        );
                    })}
                </ul>
            );
        });
        return output.length > 0 ? output : null;
    }
}

const OptionWrapper = React.forwardRef(
    (
        {
            blockName,
            index,
            value,
            label,
            emphasize,
            selected,
            hidden,
            onClick,
            children: option,
            highlightSelected
        },
        ref
    ) => {
        const liRef = useRef();
        const dataAttributes = Object.fromEntries(
            Object.entries(option.props).filter(([propName]) =>
                /^data-/.test(propName)
            )
        );

        useImperativeHandle(ref, () => ({
            index,
            li: liRef.current
        }));

        return (
            /* Предупреждения касаются a11y не-интерактивного элемента. TODO: WAI-ARIA */
            /* eslint-disable */
            <li
                ref={liRef}
                className={classNames({
                    [`${blockName}__option`]: true,
                    [`${blockName}__option--selected`]:
                        selected && highlightSelected
                })}
                /* Скрытые опции должны оставаться в документе, чтобы на них срабатывали события */
                hidden={hidden}
                onClick={onClick}
                data-index={index}
                data-value={String(value)}
                data-label={label}
                {...dataAttributes}
            >
                {React.cloneElement(option, {
                    selected,
                    emphasize,
                    onClick: (e) => {
                        e.stopPropagation();
                        onClick(e);
                    }
                })}
            </li>
            /* eslint-enable */
        );
    }
);

OptionWrapper.propTypes = {
    /**
     * Базовый CSS-класс компонента
     */
    blockName: PropTypes.string.isRequired,
    /**
     * Позиция опции в общем списке опций,
     * независимо от разделения по группам
     */
    index: PropTypes.number.isRequired,
    /* eslint-disable react/require-default-props */
    value: PropTypes.any,
    label: PropTypes.string,
    emphasize: PropTypes.func,
    selected: PropTypes.bool,
    hidden: PropTypes.bool,
    onClick: PropTypes.func,
    highlightSelected: PropTypes.bool,
    children: PropTypes.element.isRequired
    /* eslint-enable react/require-default-props */
};
