import cyrb53 from '@avtopro/cyrb53';

/**
 * Фрагмент лейбла опции, к которому в соответствии с полем
 * emphasize должна быть применена какая-то особая стилизация.
 */
export default class OptionLabelPart {
    constructor(text, emphasize) {
        this.text = text || '';
        this.emphasize = Boolean(emphasize);
    }

    /**
     * Хеш от строки для использования в качестве key в React.
     */
    get hashCode() {
        return cyrb53(this.text).toString(16);
    }

    toString() {
        return this.text;
    }
}
