import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

function PlaceholderRobot({ type }) {
    const blockName = 'pro-placeholder__robo';
    const resultClassName = classNames({
        [blockName]: true,
        [`${blockName}--${type}`]: type
    });
    return <div className={resultClassName} />;
}

PlaceholderRobot.propTypes = {
    /**
     * Режим стилизации PlaceholderRobot
     */
    type: PropTypes.oneOf([
        '',
        'lamp',
        'no-results',
        'no-shops',
        'no-warehouses',
        'no-reviews',
        'del-message',
        'no-messages',
        'no-sto',
        'no-sto-youtube',
        'youtube',
        'no-orders',
        'loader',
        'service-unavailable',
        'no-banner'
    ])
};

PlaceholderRobot.defaultProps = {
    type: ''
};

export default PlaceholderRobot;
