import React from 'react';
import PropTypes from 'prop-types';
import Placeholder from '@avtopro/placeholder';
import PlaceholderRobot from '@avtopro/placeholder-robot';
import './style/index.less';

/**
 * Прелоадер в виде робота с шестерёнками
 */
function RobotPreloader({ title, fixed }) {
    if (fixed) {
        return <div className="pro-loader" />;
    } else {
        return (
            <Placeholder>
                <PlaceholderRobot type="loader" />
                {title ? (
                    <div className="pro-placeholder__header">{title}</div>
                ) : null}
            </Placeholder>
        );
    }
}

RobotPreloader.propTypes = {
    /**
     * Подпись под роботом
     */
    title: PropTypes.string,
    /**
     * Прелоадер с шестерёнками на весь экран
     */
    fixed: PropTypes.bool
};

RobotPreloader.defaultProps = {
    title: '',
    fixed: false
};

export default RobotPreloader;
