import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import Icon from './Icon';
import './style/index.less';

/**
 * Стилизованный чекбокс
 */
export default function Checkbox(props) {
    const {
        type,
        labelText,
        blockName,
        className,
        domRef,
        ...inputProps
    } = props;

    const resultClassName = classNames(blockName, className);

    return (
        /* TODO: WAI-ARIA */
        /* eslint-disable-next-line jsx-a11y/label-has-associated-control */
        <label
            className={resultClassName}
            data-name={inputProps.name ? inputProps.name : ''}
        >
            <input type="checkbox" {...inputProps} ref={domRef} />
            <Icon className={blockName} />
            {labelText}
        </label>
    );
}

Checkbox.propTypes = {
    /**
     * Текст чекбокса. Все, что может быть отрендерено: строки, элементы
     */
    labelText: PropTypes.node,
    /**
     * Базовый CSS-класс
     * Может переопределяться в редких случаях
     */
    blockName: PropTypes.string,
    /**
     * Дополнительный CSS-класс
     */
    className: PropTypes.string,
    /**
     * Ссылка на DOM-элемент текстового поля.
     * Экспортируемый компонент проходит через React.forwardRef,
     * поэтому ссылку на поле можно получить через обычный ref.
     * @private
     */
    domRef: PropTypes.any,
    /**
     * Тип элемента формы checkbox, radio или др.
     */
    type: PropTypes.string
};

Checkbox.defaultProps = {
    labelText: '',
    blockName: 'pro-checkbox',
    className: '',
    domRef: null,
    type: 'checkbox'
};
