import React from 'react';
import classNames from 'classnames';
import Select from './Select';
import OptionsList from './OptionsList';
import ValuesList from './ValuesList';

/**
 * Компонент для выбора одной, или нескольких опций из списка.
 */
export default class List extends Select {
    static propTypes = {
        ...Select.propTypes
    };

    static defaultProps = {
        ...Select.defaultProps,

        searchable: false,
        dropdown: false,
        framed: true
    };

    render() {
        const {
            name,
            framed,
            blockName,
            blockSize,
            multiple,
            disabled,
            showGroupNames,
            renderValues,
            visibleOptionsCount
        } = this.props;
        const { selectedValues, isFocused } = this.state;
        const options = this.getMatchingOptions();

        const className = classNames({
            [blockName]: true,
            [`${blockName}--list`]: true,
            [`${blockName}--${blockSize}`]: blockSize,
            [`${blockName}--framed`]: framed,
            [`${blockName}--disabled`]: disabled,
            [`${blockName}--focused`]: isFocused,
            [`${blockName}--mobile`]: this.isMobileEnabled
        });

        return (
            <div
                className={className}
                onFocus={this.onFocus}
                onBlur={this.onBlur}
                /* eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex */
                tabIndex="0"
                /** TODO: WAI-ARIA */
                data-value={[...selectedValues].join(';')}
                data-name={this.props.name}
                ref={this.containerRef}
            >
                <OptionsList
                    blockName={blockName}
                    blockSize={blockSize}
                    options={options}
                    multiple={multiple}
                    dropdown={false}
                    showGroupNames={showGroupNames}
                    isFocused={isFocused}
                    selectedValues={selectedValues}
                    visibleOptionsCount={visibleOptionsCount}
                    onChange={this.onOptionChange}
                />
                {renderValues ? (
                    <ValuesList
                        name={name}
                        values={[...selectedValues]}
                        options={this.options}
                    />
                ) : null}
            </div>
        );
    }
}
