import React from 'react';
import PropTypes from 'prop-types';

/**
 * Информационный блок с роботом внутри модального окна.
 */
export default function ModalAlert({ children, mode }) {
    switch (mode) {
        case 'info':
            return <div className="modwin__alert">{children}</div>;
        case 'warning':
            return <div className="modwin__alert modwin__alert--warning">{children}</div>;
        case 'error':
            return <div className="modwin__alert modwin__alert--error">{children}</div>;
        case 'success':
            return <div className="modwin__alert modwin__alert--success">{children}</div>;
        default:
            return null;
    }
}

ModalAlert.propTypes = {
    children: PropTypes.any,
    mode: PropTypes.oneOf(['info', 'warning', 'error', 'success'])
};

ModalAlert.defaultProps = {
    children: undefined,
    mode: 'info'
};
