import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import './style/index.less';

/**
 * Поле для ввода однострочного, или многострочного текста.
 */
const TextInput = (props) => {
    const {
        type,
        rows,
        framed,
        blockName,
        className,
        blockSize,
        domRef,
        ...inputProps
    } = props;

    const resultClassName = classNames([
        blockName,
        className,
        { [`${blockName}--${blockSize}`]: blockSize },
        { [`${blockName}--framed`]: framed }
    ]);

    return type == 'textarea' ? (
        <textarea
            ref={domRef}
            className={resultClassName}
            rows={rows}
            {...inputProps}
        />
    ) : (
        <input
            ref={domRef}
            className={resultClassName}
            type={type}
            {...inputProps}
        />
    );
};

export default TextInput;

TextInput.propTypes = {
    /**
     * Название поля.
     */
    name: PropTypes.string,
    /**
     * Базовый CSS-класс компонента.
     */
    blockName: PropTypes.string,
    /**
     * Дополнительный CSS-класс компонента.
     */
    className: PropTypes.string,
    /**
     * Базовый размер поля (не путать с нативным HTML-атрибутом size).
     */
    blockSize: PropTypes.oneOf(['', 'sm']),
    /**
     * Есть ли у элемента рамочка.
     */
    framed: PropTypes.bool,
    /**
     * Тип поля.
     * Любой из поддерживаемых типов HTML \<input\>, или "textarea" для многострочного текста.
     * @see https://developer.mozilla.org/ru/docs/Web/HTML/Element/Input
     */
    type: PropTypes.string,
    /**
     * Количество строк, если type == "textarea".
     */
    rows: PropTypes.number,
    /**
     * Ссылка на DOM-элемент текстового поля.
     * Экспортируемый компонент проходит через React.forwardRef,
     * поэтому ссылку на поле можно получить через обычный ref.
     * @private
     */
    domRef: PropTypes.any
};

TextInput.defaultProps = {
    name: '',
    blockName: 'pro-input',
    className: '',
    blockSize: '',
    type: 'text',
    rows: 1,
    framed: true,
    domRef: null
};
