import React from 'react';
import PropTypes from 'prop-types';

/**
 * Шапка модального окна, на которой изображён робот с информационной иконкой.
 */
export default function ModalHead({ mode }) {
    switch (mode) {
        case 'info':
            return <div className="modwin__head modwin__head--info" />;
        case 'warning':
            return <div className="modwin__head modwin__head--warning" />;
        case 'error':
            return <div className="modwin__head modwin__head--error" />;
        case 'success':
            return <div className="modwin__head modwin__head--success" />;
        default:
            return null;
    }
}

ModalHead.propTypes = {
    mode: PropTypes.string
};

ModalHead.defaultProps = {
    mode: null
};
