import React from 'react';
import PropTypes from 'prop-types';
import Label from '@avtopro/label';

const LabelsList = ({ blockName, blockSize, options, onChange, onClear }) => {
    if (options.length == 0) return null;

    return (
        <div className={`${blockName}__checkbox-wrapper`}>
            {options.map((o, i) => (
                <Label
                    // eslint-disable-next-line react/no-array-index-key
                    key={`${o.props.value}_label_${i}`}
                    blockSize={blockSize}
                    onClose={(event) => {
                        event.stopPropagation();
                        onChange?.(o);
                    }}
                >
                    {o.props.label || ''}
                </Label>
            ))}
            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
            <a
                href="#"
                onClick={(event) => {
                    event.preventDefault();
                    onClear?.();
                }}
                style={{ width: '100%' }}
            >
                Удалить все
            </a>
        </div>
    );
};

LabelsList.propTypes = {
    blockName: PropTypes.string,
    blockSize: PropTypes.string,
    options: PropTypes.arrayOf(PropTypes.object),
    onChange: PropTypes.func,
    onClear: PropTypes.func
};

LabelsList.defaultProps = {
    blockName: '',
    blockSize: '',
    options: [],
    onChange: null,
    onClear: null
};

export default LabelsList;
