import React, { useLayoutEffect } from 'react';
import PropTypes from 'prop-types';
import DataList from './DataList';

const OptionsList = ({
    options,
    blockName,
    blockSize,
    multiple,
    dropdown,
    dropdownMinWidth,
    dropdownPosition,
    showGroupNames,
    isFocused,
    selectedValues,
    onChange,
    emphasizeOption,
    visibleOptionsCount,
    children
}) => {
    const wrapperRef = React.createRef();
    const listRef = React.createRef();

    // Настраиваем высоту списка опций,
    // чтобы в видимой области оказалось visibleOptionsCount опций
    useLayoutEffect(
        () => {
            const { firstLi, topLi, bottomLi } = listRef.current?.liRefs || {};
            const wrapper = wrapperRef.current;

            const getBoundingClientRect = (el, prop) =>
                el?.getBoundingClientRect()[prop] || 0;

            // Верхняя граница обёртки
            const wrapperTop = getBoundingClientRect(wrapper, 'top');
            // Нижняя граница последней видимой опции (здесь заканчивается область просмотра)
            const bottom = getBoundingClientRect(bottomLi, 'bottom');
            // Верхняя граница первой видимой опции (но не обязательно здесь будет начинаться
            // область просмотра, ведь перед опцией ещё может быть заголовок группы, или другие элементы)
            const top = getBoundingClientRect(topLi, 'top');
            // Если первая видимая опция является первой опцией в списке вообще, то добавляем к области
            // просмотра дополнительный отступ, чтобы вместить заголовки и прочие вышестоящие элементы
            const padding =
                topLi == firstLi
                    ? Math.max(
                          getBoundingClientRect(firstLi, 'top') - wrapperTop
                      )
                    : 0;

            const height = bottom - top + padding;

            if (height > 0 && wrapper) {
                wrapper.style.maxHeight = `${height}px`;
            }
        },
        // Пересчитываем высоту окна только при изменении состояния "не выбрано ничего/выбрано что-то",
        // потому что после выбора хотя бы одной опции (особенно во множественном селекте)
        // может измениться компоновка списка опций
        [selectedValues.size > 0]
    );

    // Позиционируем прокрутку области просмотра на выбранном элементе
    useLayoutEffect(() => {
        const selectedLi = listRef.current?.liRefs.selectedLi;
        const wrapper = wrapperRef.current;

        // Автопрокрутки не должно быть во множественном селекте,
        // потому что там при выборе опций выпадающий список не сворачивается,
        // да сложно понять до какой из выбранных опций скроллить
        if (selectedLi && wrapper && !multiple) {
            const {
                top: liTop,
                bottom: liBottom
            } = selectedLi.getBoundingClientRect();
            const {
                top: wrapperTop,
                bottom: wrapperBottom
            } = wrapper.getBoundingClientRect();

            // Опция скрывается за верхним краем области просмотра -
            // скроллим так, чтобы она оказалась первой в списке
            if (wrapperTop > liTop) {
                wrapper.scrollTo({
                    top: wrapper.scrollTop - (wrapperTop - liTop),
                    behavior: 'instant'
                });
            }
            // Соответственно, скрытую за нижним краем опцию позиционируем снизу
            else if (wrapperBottom < liBottom) {
                wrapper.scrollTo({
                    top: wrapper.scrollTop + (liBottom - wrapperBottom),
                    behavior: 'instant'
                });
            }
        }
    }, [multiple ? selectedValues.size > 0 : selectedValues]);

    return (
        <div
            style={
                dropdown
                    ? {
                          minWidth: dropdownMinWidth,
                          left: dropdownPosition == 'right' ? 'auto' : null,
                          right: dropdownPosition == 'left' ? 'auto' : null
                      }
                    : null
            }
            className={
                dropdown
                    ? `${blockName}__options ${blockName}__options--dropdown`
                    : `${blockName}__options`
            }
        >
            <div className={`${blockName}__options__bg`}>
                {children}
                <div
                    ref={wrapperRef}
                    className={`${blockName}__options__overflow`}
                >
                    <DataList
                        ref={listRef}
                        blockSize={blockSize}
                        isFocused={isFocused}
                        onChange={onChange}
                        selectedValues={selectedValues}
                        options={options}
                        multiple={multiple}
                        showGroupNames={showGroupNames}
                        emphasizeOption={emphasizeOption}
                        visibleOptionsCount={visibleOptionsCount}
                    />
                </div>
            </div>
        </div>
    );
};

OptionsList.propTypes = {
    /**
     * Базовый CSS-класс компонента
     */
    blockName: PropTypes.string,
    /**
     * Массив React-элементов, которые выводятся как элементы списка
     * У каждого элемента должно быть свойство value для сравнения с selectedValues
     */
    options: PropTypes.arrayOf(PropTypes.object).isRequired,
    /**
     * Объект Set с коллекцией выбранных значений
     */
    selectedValues: PropTypes.object.isRequired,
    /**
     * Показывать названия групп опций
     * Если false, то опции всё равно разделяются на отдельные списки
     */
    showGroupNames: PropTypes.bool,
    /**
     * Если компонент находится в фокусе, он отслеживает нажатия "горячих клавиш"
     */
    isFocused: PropTypes.bool,
    /**
     * Обработчик выбора опции
     */
    onChange: PropTypes.func,
    /**
     * Включить множественный выбор опций
     * Теперь множественный выбор выглядит как чекбокс
     * Свойство нужно, чтобы не добавлять класс отображение одной выбраной опции
     */
    multiple: PropTypes.bool,
    /**
     * Список опций отображается в выпадающем блоке поверх нижестоящих элементов.
     */
    dropdown: PropTypes.bool,
    /**
     * Минимальная ширина выпадающего списка.
     * По умолчанию равна 100% ширины селекта.
     */
    dropdownMinWidth: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
    /**
     * Позиционирование выпадающего списка по горизонтали.
     */
    dropdownPosition: PropTypes.oneOf(['auto', 'left', 'right']),
    /**
     * Функция обработки содержимого опции с целью визуального выделения
     * тех частей текста опции, которые совпадают с поисковым словом (query)
     */
    emphasizeOption: PropTypes.func,
    /**
     * Количество опций, которые будут одновременно видны в выпадающем списке,
     * независимо от высоты самих опций
     */
    visibleOptionsCount: PropTypes.number,
    children: PropTypes.node
};

export default OptionsList;
