import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import Select from './Select';
import Placeholder from './Placeholder';
import OptionsList from './OptionsList';
import ValuesList from './ValuesList';

/**
 * Компонент для выбора опций из выпадающего списка.
 * Стилизован под иконку, по клику на которую разворачивается список опций.
 * Не может быть searchable, т. к. отсутствует поле ввода.
 */
export default class SelectIcon extends Select {
    static propTypes = {
        ...Select.propTypes,

        /**
         * Иконка для разворачивания списка опций.
         */
        toggleIcon: PropTypes.element,
        /**
         * Размер иконки разворачивания списка опций.
         * Вместе с размером корректируются отступы.
         */
        toggleIconSize: PropTypes.oneOf(['normal', 'small']),
        /**
         * Минимальная ширина выпадающего списка.
         * По умолчанию равна 100% ширины селекта.
         */
        dropdownMinWidth: PropTypes.oneOfType([
            PropTypes.number,
            PropTypes.string
        ]),
        /**
         * Позиционирование выпадающего списка по горизонтали.
         */
        dropdownPosition: PropTypes.oneOf(['left', 'right'])
    };

    static defaultProps = {
        ...Select.defaultProps,

        blockName: 'pro-select',
        searchable: false,
        framed: false
    };

    render() {
        const {
            name,
            framed,
            disabled,
            blockName,
            blockSize,
            placeholder,
            showGroupNames,
            renderValues,
            toggleIcon,
            toggleIconSize,
            dropdownMinWidth,
            dropdownPosition,
            visibleOptionsCount
        } = this.props;
        const { selectedValues, isFocused, isOpened } = this.state;
        const options = this.getMatchingOptions();
        const selectedOptions = this.getSelectedOptions();

        const className = classNames({
            [blockName]: true,
            [`${blockName}--${blockSize}`]: blockSize,
            [`${blockName}--framed`]: framed,
            [`${blockName}--disabled`]: disabled,
            [`${blockName}--transparent`]: true,
            [`${blockName}--opened`]: isOpened && options.length > 0,
            [`${blockName}--focused`]: isFocused,
            [`${blockName}--mobile`]: this.isMobileEnabled
        });

        return (
            <div
                className={className}
                onFocus={this.onFocus}
                onBlur={this.onBlur}
                /* eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex */
                tabIndex="0"
                /** TODO: WAI-ARIA */
                data-value={[...selectedValues].join(';')}
                data-name={this.props.name}
                ref={this.containerRef}
            >
                <Placeholder
                    onChange={this.onOptionChange}
                    onClick={this.onClick}
                    onToggleClick={this.onToggleClick}
                    placeholder={placeholder}
                    multiple={false}
                    options={options}
                    selectedOptions={selectedOptions}
                    blockName={blockName}
                    blockSize={blockSize}
                    toggleIcon={toggleIcon}
                    toggleIconSize={toggleIconSize}
                >
                    <span />
                </Placeholder>
                {isOpened && !disabled ? (
                    <OptionsList
                        blockName={blockName}
                        blockSize={blockSize}
                        options={options}
                        multiple={false}
                        dropdown={this.isDropdownEnabled}
                        dropdownMinWidth={dropdownMinWidth}
                        dropdownPosition={dropdownPosition}
                        showGroupNames={showGroupNames}
                        isFocused={isFocused}
                        selectedValues={selectedValues}
                        visibleOptionsCount={visibleOptionsCount}
                        onChange={this.onOptionChange}
                    />
                ) : null}
                {renderValues ? (
                    <ValuesList
                        name={name}
                        values={[...selectedValues]}
                        options={this.options}
                    />
                ) : null}
            </div>
        );
    }
}
