import React from 'react';

const ValuesList = ({ name, values, options }) => {
    if (values.length > 0) {
        return values.map((value) => (
            <input
                key={`${name}_hidden_${value}`}
                type="hidden"
                name={name}
                value={value}
            />
        ));
    }
    // Вывод первой опции, как значения по умолчанию
    if (options.length > 0) {
        return (
            <input type="hidden" name={name} value={options[0].props.value} />
        );
    }
    return null;
};

export default ValuesList;
