import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import './style/index.less';

/**
 * Стилизованная универсальная кнопка.
 */
export default function Button(props) {
    const {
        color,
        theme,
        uppercase,
        framed,
        square,
        children,
        blockName,
        className,
        blockSize,
        domRef, // eslint-disable-line react/prop-types
        ...buttonProps
    } = props;

    // Обратная совместимость
    // TODO: убрать, когда все color будут заменены на theme
    const themeTmp = theme || color;
    const resultClassName = classNames(
        blockName,
        className,
        { [`${blockName}--${themeTmp}`]: Boolean(themeTmp) },
        { [`${blockName}--${blockSize}`]: blockSize },
        { [`${blockName}--uppercase`]: uppercase },
        { [`${blockName}--framed`]: framed },
        { [`${blockName}--square`]: square }
    );
    return (
        /* eslint-disable-next-line react/button-has-type */
        <button ref={domRef} className={resultClassName} {...buttonProps}>
            {children}
        </button>
    );
}

Button.propTypes = {
    /**
     * Старое название свойства "theme".
     * @deprecated
     */
    color: PropTypes.any, // eslint-disable-line
    /**
     * Стиль кнопки.
     */
    theme: PropTypes.oneOf([
        '',
        'prime',
        'white',
        'light-blue',
        'blue',
        'danger',
        'link',
        'youtube',
        'telegram',
        'inverse'
    ]),
    /**
     * Базовый CSS-класс.
     * Может переопределяться в редких случаях.
     */
    blockName: PropTypes.string,
    /**
     * Дополнительный CSS-класс.
     */
    className: PropTypes.string,
    /**
     * Базовый размер кнопки.
     */
    blockSize: PropTypes.oneOf(['', 'sm']),
    /**
     * Привести текст внутри кнопки к верхнему регистру.
     * Используйте это свойство вместо передачи текста в верхнем регистре через children,
     * потому что к uppercase-кнопке будут применены специальные стили.
     */
    uppercase: PropTypes.bool,
    /**
     * Есть ли у элемента рамочка.
     */
    framed: PropTypes.bool,
    /**
     * Кнопка с одинаковыми полями сверху/снизу/справа/слева.
     */
    square: PropTypes.bool,
    /**
     * Содержимое кнопки.
     */
    children: PropTypes.any
};

Button.defaultProps = {
    theme: '',
    framed: true,
    square: false,
    uppercase: false,
    blockName: 'pro-btn',
    className: '',
    blockSize: '',
    children: null
};
