import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import isMobile from 'is-mobile';
import memoize from 'memoize-one';
import Placeholder from './Placeholder';
import Search from './Search';
import OptionsList from './OptionsList';
import LabelsList from './LabelsList';
import ValuesList from './ValuesList';
import OptionLabelPart from './OptionLabelPart';
import './style/index.less';

/**
 * Компонент для выбора опций из выпадающего списка.
 * @TODO
 * Может быть проблема с переключением в контролируемый режим
 * ввода value и query, если их значения undefined.
 * @TODO WAI-ARIA.
 */
export default class Select extends React.Component {
    static propTypes = {
        /**
         * Название поля (используется, если renderValues == true).
         */
        name: PropTypes.string,
        /**
         * Начальное значение поля.
         */
        defaultValue: PropTypes.oneOfType([PropTypes.any, PropTypes.array]),
        /**
         * Управляемое значение поля, перекрывает defaultValue.
         */
        value: PropTypes.oneOfType([PropTypes.any, PropTypes.array]),
        /**
         * Начальное значение поискового слова.
         */
        defaultQuery: PropTypes.string,
        /**
         * Управляемое значение поискового слова, перекрывает defaultQuery.
         */
        query: PropTypes.string,
        /**
         * Текст, который выводится пока ни одна опция не выбрана и поле поиска не активировано.
         */
        placeholder: PropTypes.string,
        /**
         * Базовый CSS-класс компонента для кастомизации.
         */
        blockName: PropTypes.string,
        /**
         * Дополнительный CSS-класс для кастомизации
         */
        className: PropTypes.string,
        /**
         * Базовый размер поля.
         */
        blockSize: PropTypes.oneOf(['', 'sm']),
        /**
         * Флаг "мобильный вид", если нужно управлять видом из родительского компонента.
         */
        isMobile: PropTypes.bool,
        /**
         * Есть ли у элемента рамочка.
         */
        framed: PropTypes.bool,
        /**
         * Список опций отображается в выпадающем блоке поверх нижестоящих элементов.
         */
        dropdown: PropTypes.bool,
        /**
         * Включить множественный выбор опций.
         */
        multiple: PropTypes.bool,
        /**
         * Включить фильтрацию опций по поисковому запросу.
         */
        searchable: PropTypes.bool,
        /**
         * Деактивировать компонент, не взаимодействовать с пользователем.
         */
        disabled: PropTypes.bool,
        /**
         * Вставлять выбранные значения в документ скрытыми полями.
         */
        renderValues: PropTypes.bool,
        /**
         * Селект передаёт введённое в поисковую строку значение в onChange,
         * что позволяет получать значения, изначально отсутствующие в списке.
         */
        acceptsUserOption: PropTypes.bool,
        /**
         * Показывать названия групп опций. Если false, то опции
         * всё равно разделяются на отдельные списки, но без заголовков.
         */
        showGroupNames: PropTypes.bool,
        /**
         * Обработчик изменения выбора опций.
         */
        onChange: PropTypes.func,
        /**
         * Обработчик изменения поискового запроса (для автодополнения).
         */
        onQueryChange: PropTypes.func,
        /**
         * Обработчик изменения внутреннего состояния компонента:
         * Поддерживается обработка событий "focus", "open", "active"
         * onStateChange(<string>eventName, <any>eventValue).
         */
        onStateChange: PropTypes.func,
        /**
         * Список опций.
         */
        children: PropTypes.any,
        /**
         * Иконка разворачивания списка опций.
         */
        toggleIcon: PropTypes.element,
        /**
         * Размер иконки разворачивания списка опций.
         * Вместе с размером корректируются отступы.
         */
        toggleIconSize: PropTypes.oneOf(['normal', 'small']),
        /**
         * Минимальная ширина выпадающего списка.
         * По умолчанию равна 100% ширины селекта.
         */
        dropdownMinWidth: PropTypes.oneOfType([
            PropTypes.number,
            PropTypes.string
        ]),
        /**
         * Позиционирование выпадающего списка по горизонтали.
         */
        dropdownPosition: PropTypes.oneOf(['auto', 'left', 'right']),
        /**
         * Количество опций, которые будут одновременно видны в выпадающем списке,
         * независимо от высоты самих опций.
         */
        visibleOptionsCount: PropTypes.number,
        /**
         * Свойство позволяет октлючить лейблы во множественном селекте
         */
        renderLabels: PropTypes.bool
    };

    static defaultProps = {
        name: '',
        defaultValue: undefined,
        value: undefined,
        defaultQuery: undefined,
        query: undefined,
        placeholder: undefined,
        blockName: 'pro-select',
        className: '',
        blockSize: '',
        isMobile: undefined,
        framed: true,
        dropdown: true,
        multiple: false,
        searchable: false,
        disabled: false,
        renderValues: false,
        acceptsUserOption: false,
        showGroupNames: true,
        onChange: undefined,
        onQueryChange: undefined,
        onStateChange: undefined,
        children: undefined,
        toggleIcon: undefined,
        toggleIconSize: 'normal',
        dropdownMinWidth: undefined,
        dropdownPosition: 'auto',
        visibleOptionsCount: undefined,
        renderLabels: true
    };

    /* eslint-disable react/sort-comp */

    /**
     * @see https://reactjs.org/docs/react-component.html#static-getderivedstatefromprops
     */
    static getDerivedStateFromProps(props) {
        const newState = {};

        if (props.query !== undefined) {
            newState.query = Select.parseQuery(props.query);
        }

        if (props.value !== undefined) {
            newState.selectedValues = Select.parseValue(props.value);
        }

        return Object.keys(newState).length > 0 ? newState : null;
    }

    /**
     * Преобразует полученное извне значение в строку query.
     * @param {*} value
     * @returns {string}
     */
    static parseQuery = memoize((value) => (value ? value.toString() : ''));

    /**
     * Преобразует полученное извне значение в набор значений selectedValues.
     * @param {string|Array} value
     * @returns {Set}
     */
    static parseValue = (value) =>
        value != null
            ? new Set(Array.isArray(value) ? value : [value])
            : new Set();

    /**
     * Экранирует спецсимволы RegExp в поисковом слове.
     * @param {string} value - поисковое слово
     * @param {string} [flags] - флаги регулярного выражения
     * @returns {RegExp}
     */
    static makeQueryRegExp = memoize((value, flags = 'i') => {
        const safeQuery = value.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
        return new RegExp(`(${safeQuery})`, flags);
    });

    constructor(props) {
        super(props);

        this.state = {
            isFocused: false, // Фокус документа находится на компоненте
            isOpened: false, // Список опций раскрыт
            isActive: false, // Компонент готов принимать ввод query
            query: Select.parseQuery(props.defaultQuery),
            selectedValues: Select.parseValue(props.defaultValue)
        };

        this.containerRef = React.createRef();
        this.queryInputRef = React.createRef();
    }

    componentDidMount() {
        document.addEventListener('keydown', this.onKeyDown);
    }

    componentDidUpdate(prevProps, prevState) {
        if (this.props.searchable) {
            const container = this.containerRef.current;
            const queryInput = this.queryInputRef.current;
            // Переводим фокус на появившееся поле ввода.
            // Не используем autoFocus, потому что в момент, когда он срабатывает,
            // компонент ещё не польностью отрендерен и ссылка на this.container == null
            if (this.state.isActive && queryInput) {
                queryInput.focus();
            }
            // После удаления поля ввода возвращаем фокус на контейнер,
            // иначе компонент полностью потеряет фокус и при этом не сработает onBlur
            // Без проверки prevState.isActive тоже работает, но часто вызывается без необходимости
            else if (prevState.isActive && this.state.isFocused && container) {
                container.focus();
            }
        }

        // В компонент заложено условие, по которому поисковое слово (query)
        // сбрасывается после изменения опции, см. метод onOptionChange.
        // Это условие должно соблюдаться не только при выборе опции из списка
        // но и при передаче нового значения извне, из управляющего компонента.
        // При этом управляющий компонент, передающий value, не обязательно
        // берёт на себя управление значением query, поэтому мы вынуждены
        // самостоятельно определять ситуацию, когда query требует очистки.
        if (
            !this.props.multiple &&
            prevProps.value != this.props.value &&
            this.state.query != '' &&
            !this.props.query
        ) {
            // eslint-disable-next-line react/no-did-update-set-state
            this.setState({
                query: ''
            });
        }

        if (this.props.onStateChange) {
            const { isFocused, isOpened, isActive } = this.state;
            const {
                isFocused: wasFocused,
                isOpened: wasOpened,
                isActive: wasActive
            } = prevState;

            if (isFocused !== wasFocused)
                this.props.onStateChange('focus', isFocused);
            if (isOpened !== wasOpened)
                this.props.onStateChange('open', isOpened);
            if (isActive !== wasActive)
                this.props.onStateChange('active', isActive);
        }
    }

    componentWillUnmount() {
        document.removeEventListener('keydown', this.onKeyDown);
    }

    get isSearchEnabled() {
        const { searchable: isSearchable } = this.props;
        const { isActive, isFocused } = this.state;
        return isSearchable && isActive && isFocused;
    }

    get isMobileEnabled() {
        const { isMobile: isMobileProp } = this.props;
        return isMobileProp === undefined ? isMobile() : isMobileProp;
    }

    get isDropdownEnabled() {
        const { dropdown } = this.props;
        // В мобильном виде опции всегда выводятся статическим списком,
        // вместо абсолютно позиционированного выпадающего списка
        return !this.isMobileEnabled && dropdown;
    }

    get options() {
        // Вызываемая функция обязательно должна быть мемоизована,
        // иначе постоянные обращения к options будут создавать немалую нагрузку
        return this.mapOptions(this.props.children);
    }

    /**
     * Нормализует свойства опций: возвращает копии дочерних элементов,
     * у которых label и value всегда заполнены.
     * Мемоизация обязательна — метод вызывается при каждом обращении к this.options!
     * @param {*} children - вложенные в Select опции
     */
    mapOptions = memoize((childOptions) => {
        const options = React.Children.map(childOptions, (child) => {
            // Отбрасываем текстовые ноды и прочее
            if (React.isValidElement(child) == false) return null;

            const {
                label: rawLabel,
                value: rawValue,
                group,
                children
            } = child.props;

            const label =
                [
                    rawLabel,
                    typeof children == 'string' ? children : '',
                    rawValue
                ]
                    .find((v) => v)
                    ?.toString() ?? '';

            // Допускается null в качестве значения опции
            const value = rawValue !== undefined ? rawValue : label;

            return React.cloneElement(child, {
                label,
                value,
                group
            });
        });

        return (options || []).filter((o) => o);
    });

    /**
     * Разбивает текст опции на блоки, выполняя сопоставление с поисковым выражением.
     * Блоки, текст которых совпал с поиском, получают значение emphasize = true
     * и могут быть особым образом стилизованы компонентом Option.
     * @param {string} label - текст опции, по которому выполняется поиск
     * @returns {OptionLabelPart[]} - массив блоков, или null, если поиск невозможен
     */
    emphasizeOption = (label) => {
        const { searchable } = this.props;
        const { query } = this.state;

        if (searchable && query && label) {
            const queryRegExp = Select.makeQueryRegExp(query, 'gi');
            return label
                .split(queryRegExp)
                .filter((text) => text)
                .map(
                    (text) => new OptionLabelPart(text, queryRegExp.test(text))
                );
        }
        if (searchable && label) {
            return [new OptionLabelPart(label)];
        }
        if (searchable) {
            return [];
        }
        return null;
    };

    /**
     * Возвращает опции, совпадающие с поисковым запросом,
     * или те опции, label которых равен "*".
     */
    getMatchingOptions() {
        const queryRegExp = Select.makeQueryRegExp(this.state.query);
        return this.options.filter((option) => {
            const { label } = option.props;
            return label == '*' || queryRegExp.test(label);
        });
    }

    /**
     * Возвращает выбранные опции.
     */
    getSelectedOptions() {
        return this.options.filter((option) =>
            this.state.selectedValues.has(option.props.value)
        );
    }

    /**
     * Применяет опцию, описание которой полностью
     * совпадает с запросом (query),
     * либо же сообщает введённый запрос родителю
     * в качестве нового значения компонента.
     */
    applyMatchingOption() {
        const { query } = this.state;
        const { name, multiple, acceptsUserOption } = this.props;
        const options = this.getMatchingOptions();

        if (query && acceptsUserOption) {
            this.props.onChange?.(name, [query]);
            return;
        }

        // Единственная опция применяется только
        // если поисковый запрос не пустой.
        // При множественном выборе это выглядит нелогичным.
        if (query && !multiple && options.length == 1) {
            this.onOptionChange(options[0]);
        }
    }

    /**
     * Обрабатывает изменение поискового запроса.
     * Здесь можно встраивать автодополнение - внешний компонент
     * обрабатывает запрос и присылает новые опции.
     */
    onQueryChange = (e) => {
        const { onQueryChange, disabled } = this.props;

        if (disabled) {
            return;
        }

        this.setState({
            query: e.target.value,
            isOpened: true
        });

        if (onQueryChange) {
            onQueryChange(e.target.value);
        }
    };

    /**
     * Обрабатывает изменение выбора опций.
     * @param {Object} option
     * @param {boolean} [single] - если true, должна быть выбрана только переданная опция,
     *                             даже при множественном выборе
     */
    onOptionChange = (option, single) => {
        const { value } = option?.props || {};
        const { multiple, disabled } = this.props;

        if (disabled) {
            return;
        }

        const selectedValues =
            single || !multiple
                ? Select.parseValue(null)
                : Select.parseValue([...this.state.selectedValues]);

        // Добавляем новое значение, игнорируя пустые опции
        // Опции со значениями 0, null, false не считаются пустыми
        if (value !== undefined) {
            // При множественном выборе повторное нажатие на выбранную опцию
            // приводит к её сбросу. Это можно сделать отдельной настройкой,
            // не фиксируясь исключительно на множественном выборе.
            if (multiple && selectedValues.has(value)) {
                selectedValues.delete(value);
            } else {
                selectedValues.add(value);
            }
        }

        this.setState({
            query: '',
            selectedValues,
            isActive: false,
            isOpened: single ? this.state.isOpened : multiple == true
        });

        if (this.props.onChange) {
            this.props.onChange(this.props.name, [...selectedValues]);
        }
    };

    /**
     * Вероятно, этот метод используется имеративно для активации поиска.
     * @todo найти применение этого метода.
     * @todo переименовать в "activate".
     */
    active = () => {
        this.setState({
            isActive: true,
            isOpened: true,
            isFocused: true
        });
    };

    open() {
        this.setState({
            isOpened: true
        });
    }

    close() {
        this.setState({
            isActive: false,
            isOpened: false
        });
    }

    focus() {
        if (this.state.isFocused == false) {
            this.setState({
                isFocused: true,
                isActive: this.state.query != ''
            });
        }
    }

    /**
     * Обрабатывает фокусировку на поле.
     */
    onFocus = () => {
        this.focus();
    };

    /**
     * Обрабатывает фокусировку на поле ввода.
     * Останавливает всплытие события, если фокус уже находится на элементе.
     * Это необязательно, но позволяет избежать лишних вызовов onBlur и onFocus.
     */
    onInputFocus = (e) => {
        if (this.state.isFocused) {
            e.stopPropagation();
        }
    };

    /**
     * Обрабатывает нажатие на компонент, по которому
     * он переходит в активное состояние.
     */
    onClick = () => {
        const isSearchable = this.props.searchable;
        this.setState({
            isActive: isSearchable,
            isFocused: true,
            isOpened: isSearchable ? true : !this.state.isOpened
        });
    };

    /**
     * Обрабатывает сворачивание/разворачивание селекта.
     * Всплытие блокируется, чтобы не срабатывал onClick.
     */
    onToggleClick = (e) => {
        e.stopPropagation();
        this.setState({
            isFocused: true,
            isOpened: !this.state.isOpened
        });
    };

    /**
     * Обрабатывает уход фокуса с поля - компонент закрывается и деактивируется.
     * Переход фокуса на вложенные элементы должен игнорироваться.
     */
    onBlur = (e) => {
        const rt = e.relatedTarget || (document && document.activeElement);
        const { query } = this.state;
        const { value } = this.props;

        if (rt === null || (rt && e.currentTarget.contains(rt) == false)) {
            if (query != value) {
                this.applyMatchingOption();
            }
            this.setState({
                isFocused: false,
                isActive: false,
                isOpened: false
            });
        }
    };

    /**
     * Обрабатывает закрытие списка опций при выводе через Overlay.
     */
    onClose = () => {
        this.close();
    };

    /**
     * Обрабатывает очистку выбраных опций (реализовано во множественном выборе).
     */
    onClear = () => {
        this.onOptionChange(null, true);
    };

    onKeyDown = (e) => {
        if (this.state.isFocused && e.target == this.containerRef.current) {
            const { isActive, isOpened } = this.state;

            switch (e.keyCode) {
                // Enter:
                // - если поиск активен и доступна всего одна опция - она применяется;
                // - если поиск активен, но опций много - ничего не происходит;
                // - если поиск неактивен и открыт - закрывается;
                // - если поиск неактивен и закрыт - открывается в активном состоянии.
                case 13:
                    if (isActive) {
                        this.applyMatchingOption();
                    } else {
                        this.setState({
                            isActive: !isOpened, // так и задумано!
                            isOpened: !isOpened
                        });
                    }
                    break;

                // Esc: деактивирует компонент
                case 27:
                    this.setState({
                        isActive: false,
                        isOpened: false
                    });
                    break;

                // Backspace: только при неактивном поиске стирает query
                // Если поиск активен, backspace не должен перехватываться
                case 8:
                    if (!isActive && this.state.query) {
                        this.setState({
                            query: '',
                            isActive: true
                        });
                    }
                    break;

                // Space: только при неактивном поиске разворачивает список опций
                // Если поиск активен, space не должен перехватываться
                case 32:
                    if (!isActive) {
                        e.preventDefault();
                        this.setState({
                            isOpened: true
                        });
                    }
                    break;

                // Если нажимается какая-либо символьная клавиша - активируется поиск,
                // причём ввод должен начинаться заново, стирая предыдущий запрос.
                // Для определения символьных клавиш вообще-то есть свойства code и charCode,
                // но в SyntheticEvent при keydown они почему-то не заполнены,
                // хотя по стандарту должны: https://developer.mozilla.org/ru/docs/Web/Events/keydown
                //
                // (!) При автозаполнении формы возникает странное событие "keydown",
                // в котором нет никакой информации о клавише. Проверка на null обязательна.
                default:
                    if (e.key?.length == 1 && !isActive) {
                        this.setState({
                            query: '',
                            isActive: true
                        });
                    }
            }
        }
    };

    render() {
        const {
            name,
            framed,
            blockName,
            className,
            blockSize,
            placeholder,
            multiple,
            disabled,
            showGroupNames,
            renderValues,
            toggleIcon,
            toggleIconSize,
            dropdownMinWidth,
            dropdownPosition,
            visibleOptionsCount,
            renderLabels
        } = this.props;
        const { selectedValues, isFocused, isOpened, query } = this.state;
        const options = this.getMatchingOptions();
        const selectedOptions = this.getSelectedOptions();

        const resultClassName = classNames(className, {
            [blockName]: true,
            [`${blockName}--${blockSize}`]: blockSize,
            [`${blockName}--framed`]: framed,
            [`${blockName}--disabled`]: disabled,
            [`${blockName}--opened`]: isOpened && options.length > 0,
            [`${blockName}--focused`]: isFocused,
            [`${blockName}--mobile`]: this.isMobileEnabled
        });

        return (
            <div
                className={resultClassName}
                onFocus={this.onFocus}
                onBlur={this.onBlur}
                /* eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex */
                tabIndex="0"
                data-value={[...selectedValues].join(';')}
                data-name={this.props.name}
                ref={this.containerRef}
            >
                <Placeholder
                    onChange={this.onOptionChange}
                    onClick={this.onClick}
                    onToggleClick={this.onToggleClick}
                    placeholder={placeholder}
                    disabled={disabled}
                    multiple={multiple}
                    withLabels={multiple && !isOpened && renderLabels}
                    options={options}
                    selectedOptions={selectedOptions}
                    blockName={blockName}
                    blockSize={blockSize}
                    toggleIcon={toggleIcon}
                    toggleIconSize={toggleIconSize}
                >
                    {this.isSearchEnabled ? (
                        <Search
                            ref={this.queryInputRef}
                            value={query}
                            onChange={this.onQueryChange}
                            onFocus={this.onInputFocus}
                            blockName={blockName}
                        />
                    ) : null}
                </Placeholder>
                {isOpened && !disabled ? (
                    <OptionsList
                        blockName={blockName}
                        blockSize={blockSize}
                        options={options}
                        multiple={multiple}
                        dropdown={this.isDropdownEnabled}
                        dropdownMinWidth={dropdownMinWidth}
                        dropdownPosition={dropdownPosition}
                        showGroupNames={showGroupNames}
                        isFocused={isFocused}
                        selectedValues={selectedValues}
                        emphasizeOption={this.emphasizeOption}
                        visibleOptionsCount={visibleOptionsCount}
                        onChange={this.onOptionChange}
                    >
                        {multiple && renderLabels ? (
                            <LabelsList
                                blockName={blockName}
                                blockSize={blockSize}
                                options={[...selectedOptions]}
                                onChange={this.onOptionChange}
                                onClear={this.onClear}
                            />
                        ) : null}
                    </OptionsList>
                ) : null}
                {renderValues ? (
                    <ValuesList
                        name={name}
                        values={[...selectedValues]}
                        options={this.options}
                    />
                ) : null}
            </div>
        );
    }
}
