import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import Cross from '@avtopro/icons/dist/jsx/CrossIcon';

export default function Label({ onClose, disabled, blockSize, children }) {
    const blockName = 'pro-label-block';
    return (
        <div
            className={classNames(blockName, {
                [`${blockName}--${blockSize}`]: blockSize,
                [`${blockName}--disabled`]: disabled
            })}
        >
            <span>{children}</span>
            {onClose && !disabled ? (
                <span
                    role="button"
                    tabIndex={0}
                    className={`${blockName}__icon`}
                    onClick={onClose}
                    onKeyDown={onClose}
                >
                    <Cross />
                </span>
            ) : null}
        </div>
    );
}

Label.propTypes = {
    /**
     * Базовый размер лейбла.
     */
    blockSize: PropTypes.oneOf(['', 'sm']),
    /**
     * Внутреннее содержимое лейбла.
     */
    children: PropTypes.any.isRequired,
    /**
     * Лейбл неактивен и не реагирует на нажатия.
     */
    disabled: PropTypes.bool,
    /**
     * Обработчик нажатия на кнопку удаления лейбла.
     */
    onClose: PropTypes.func
};

Label.defaultProps = {
    blockSize: '',
    disabled: false,
    onClose: null
};
