import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import './style/index.less';

function Placeholder({ children, title, className, ...restProps }) {
    const resultClassName = classNames('pro-placeholder', className);

    return (
        <div className={resultClassName} {...restProps}>
            {!title ? null : (
                <div className="pro-placeholder__header pro-placeholder__header--bold">
                    {title}
                </div>
            )}
            {children}
        </div>
    );
}

Placeholder.propTypes = {
    /**
     * Содержимое компонента
     */
    children: PropTypes.node,
    /**
     * Заголовок
     */
    title: PropTypes.string.isRequired,
    /**
     * Дополнительный CSS-класс
     */
    className: PropTypes.string
};

Placeholder.defaultProps = {
    children: null,
    className: ''
};

export default Placeholder;
