import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import Checkbox from '@avtopro/checkbox';

/**
 * Опция для компонентов Select и List.
 * Предназначена для документирования и валидации props.
 * Содержимое опции без изменений выводится в виде элемента списка
 * и через placeholder компонента Select после выбора опции.
 */
export function Option({ children, stopClickPropagation, ...props }) {
    const { blockName, className, value, label, emphasize } = props;

    const buildInnerHtml =
        typeof children == 'function'
            ? children
            : () => {
                  // Нужно учесть, что контент опции может быть не строковым,
                  // и что опция может иметь label, не совпадающий с контентом.
                  // В связи с этим мы в стандартной опции не будем пытаться
                  // делать выделение поискового слова в тексте при всех условиях,
                  // это всегда можно сделать через кастомизацию опции
                  // под конкретный случай.
                  // А по умолчанию выделение текста будет работать при полном совпадении
                  // label и контента (в т. ч. когда label явно не задан).
                  if (label === children && emphasize) {
                      return (
                          emphasize(label)?.map((block, i) =>
                              block.emphasize ? (
                                  <b
                                      // В этом случае применение i неизбежно, т. к. фрагмент текста
                                      // может встречаться в одной опции несколько раз ("Niss->an<- Terr->an<-o"),
                                      // поэтому комбинация value + hashCode('an') не даст уникального ключа.
                                      /* eslint-disable react/no-array-index-key */
                                      key={`${value}_${block.hashCode}_${i}`}
                                      className={`${blockName}__option-emph`}
                                  >
                                      {block.text}
                                  </b>
                              ) : (
                                  block.text
                              )
                          ) || children
                      );
                  }
                  return children;
              };

    return (
        /* eslint-disable-next-line jsx-a11y/no-static-element-interactions, jsx-a11y/click-events-have-key-events */
        <div
            className={classNames(blockName, className)}
            onClick={
                stopClickPropagation ? (e) => e.stopPropagation() : undefined
            }
        >
            {buildInnerHtml(props)}
        </div>
    );
}

/* eslint-disable react/no-unused-prop-types */
Option.propTypes = {
    /**
     * Описание опции, которое можно использовать для поиска.
     * Если не указано явно, то будет взято из дочернего текстового элемента.
     * Если label равен "*", опция будет выводиться всегда, при любом поиске.
     */
    label: PropTypes.string,
    /**
     * Значение опции.
     * Если не указано — приравнивается к label.
     */
    value: PropTypes.any,
    /**
     * Обработчик выбора опции, который позволит опции сообщать о выборе себя,
     * если событие происходит от каких-то внутренних элементов опции (встроенный чекбокс, кнопка).
     */
    onClick: PropTypes.func,
    /**
     * Остановить всплытие произошедших в опции кликов.
     * Так как выбор опции автоматически происходит после клика на любой
     * из вложенных в неё элементов, это может стать помехой в особых случаях, когда применяется
     * явный вызов onClick. В таких случаях нужно "погасить" всплытие
     * клика до вышестоящего обработчика и самостоятельно вызвать onClick по своим условиям.
     */
    stopClickPropagation: PropTypes.bool,
    /**
     * Возвращает описание опции, разбитое на фрагменты по совпадению с поисковым словом (query).
     * Например, описание "Гидромеханика" и query "мех" дают такие фрагменты:
     * [
     *   {"text": "Гидро", "emphasize": false},
     *   {"text": "мех", "emphasize": true},
     *   {"text": "аническая", "emphasize": false}
     * ],
     * где "мех" отмечен emphasize: true, что можно использовать для выделения
     * этого фрагмента в тексте опции.
     * @type {OptionLabelPart[]}
     */
    emphasize: PropTypes.func,
    /**
     * Опция выбрана?
     * Значение устанавливается компонентом Select автоматически.
     */
    selected: PropTypes.bool,
    /**
     * Опция использована в качестве placeholder?
     * Значение устанавливается компонентом Select автоматически.
     */
    placed: PropTypes.bool,
    /**
     * Базовый CSS-класс.
     */
    blockName: PropTypes.string,
    /**
     * Дополнительный CSS-класс для кастомизации.
     */
    className: PropTypes.string,
    /**
     * Название группы, к которой относится опция.
     */
    group: PropTypes.string,
    /**
     * Содержимое опции: текст, иконки, всё что может быть отрендерено,
     * или функция для гибкого управления содержимым опции.
     */
    children: PropTypes.oneOfType([PropTypes.node, PropTypes.func])
};

Option.defaultProps = {
    label: undefined,
    value: undefined,
    onClick: undefined,
    stopClickPropagation: false,
    emphasize: undefined,
    selected: false,
    placed: false,
    blockName: 'pro-select__option',
    className: '',
    group: '',
    children: undefined
};

/**
 * Опция выбора цвета.
 */
export function ColorOption({ hexCode, value, label, children, ...props }) {
    const style = {
        background: hexCode || 'none'
    };
    return (
        <Option
            blockName="pro-select__color-option"
            value={value}
            label={label}
            {...props}
        >
            <i style={style} />
            <span>{children}</span>
        </Option>
    );
}

ColorOption.propTypes = {
    /**
     * HEX-код цвета для превьюшки.
     */
    hexCode: PropTypes.string.isRequired,
    /**
     * Номер цвета в палитре.
     */
    value: PropTypes.number.isRequired,
    /**
     * Кроткое название цвета ("Красный").
     */
    label: PropTypes.string.isRequired,
    /**
     * Полное название цвета ("GM Красный мет.").
     */
    children: PropTypes.string.isRequired
};

/**
 * Опция с возможностью вывода содержимого в многострочном виде.
 */
export function MultilineOption({ className, children, ...props }) {
    return (
        <Option
            className={classNames(className, 'pro-select__multiline-option')}
            {...props}
        >
            {props.placed && props.labelPlaceholder ? props.label : children}
        </Option>
    );
}

MultilineOption.propTypes = {
    ...Option.propTypes,
    label: PropTypes.string.isRequired,
    labelPlaceholder: PropTypes.bool
};

MultilineOption.defaultProps = {
    labelPlaceholder: true
};

MultilineOption.displayName = 'MultilineOption';

/**
 * Опция с чекбоксом, предназначается для множественного выбора.
 */
export function CheckboxOption({ children, selected, onClick, ...props }) {
    if (props.placed)
        return (
            <Option selected={selected} {...props}>
                {children}
            </Option>
        );
    else
        return (
            <Option
                blockName=""
                selected={selected}
                stopClickPropagation
                {...props}
            >
                <Checkbox
                    className="pro-select__option pro-select__checkbox-option"
                    labelText={children}
                    checked={selected}
                    onChange={onClick}
                />
            </Option>
        );
}

CheckboxOption.propTypes = Option.propTypes;

/**
 * Опция выбора товара (запчасти, или чего-то другого).
 */
export function GoodsOption({
    picture,
    code,
    brand,
    description,
    count,
    price,
    ...props
}) {
    /* TODO: CSS in JS */
    return (
        <MultilineOption {...props}>
            <div
                style={{
                    display: 'grid',
                    gridTemplateColumns: 'auto 1fr',
                    columnGap: 10,
                    alignItems: 'center'
                }}
            >
                <div
                    style={{
                        gridRowStart: 1,
                        gridRowEnd: 4
                    }}
                >
                    <div
                        style={{
                            width: 66,
                            height: 46,
                            padding: 2,
                            border: '1px solid #EDEDED',
                            boxSizing: 'border-box',
                            background: '#FFF'
                        }}
                    >
                        {/* TODO: применить FilePreview */}
                        {picture}
                    </div>
                </div>
                <div>
                    <strong>{code}</strong> <span>{brand}</span>
                </div>
                <small style={{ color: '#7B7B7B', lineHeight: 1.45 }}>
                    {description}
                </small>
                {/* TODO: i18n */}
                <div>{`${count} шт. ${price}`}</div>
            </div>
        </MultilineOption>
    );
}

GoodsOption.propTypes = {
    ...MultilineOption.propTypes,
    picture: PropTypes.node,
    code: PropTypes.string.isRequired,
    brand: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    count: PropTypes.number.isRequired,
    price: PropTypes.string.isRequired
};

GoodsOption.defaultProps = {
    picture: null
};
