/* eslint-disable react/prop-types */
import React, { useMemo } from 'react';
// eslint-disable-next-line no-unused-vars
import PropTypes from 'prop-types';
import ArrowSelectIcon from '@avtopro/icons/dist/jsx/ArrowSelectIcon';
import ArrowSelectSmIcon from '@avtopro/icons/dist/jsx/ArrowSelectSmIcon';
import { Option } from './Option';
import './style/index.less';

const Placeholder = ({
    onChange,
    onClick,
    onToggleClick,
    placeholder,
    disabled,
    options,
    selectedOptions,
    multiple,
    withLabels,
    blockName,
    blockSize,
    children,
    toggleIcon,
    toggleIconSize
}) => {
    const placeholderElement = useMemo(() => {
        if (multiple && selectedOptions.length > 0) {
            return (
                <Option className={`${blockName}__option`}>
                    {selectedOptions
                        .map((selection) => selection.props.label)
                        .join(', ')}
                </Option>
            );
        }
        if (!multiple && selectedOptions.length > 0) {
            return React.cloneElement(selectedOptions[0], {
                selected: true,
                placed: true
            });
        }
        if (placeholder) {
            return (
                <Option className={`${blockName}__option`}>
                    {placeholder}
                </Option>
            );
        }
        return '';
    }, [
        selectedOptions,
        multiple,
        withLabels,
        placeholder,
        blockName,
        onChange
    ]);

    return (
        /* eslint-disable jsx-a11y/no-static-element-interactions, jsx-a11y/click-events-have-key-events */
        <div className={`${blockName}__group`} onClick={onClick}>
            {children || (
                <div className={`${blockName}__placeholder`}>
                    {placeholderElement}
                </div>
            )}
            {
                // Кнопка toggle выводится в состоянии disabled, независимо от кол-ва опций,
                // чтобы наглядно показать что перед нами поле типа select
                (options.length > 0 || disabled) && toggleIcon !== null ? (
                    <span
                        role="button"
                        className={`${blockName}__toggle ${blockName}__toggle--${toggleIconSize}`}
                        onClick={onToggleClick}
                        tabIndex="-1"
                    >
                        {toggleIcon ||
                            (blockSize == 'sm' ? (
                                <ArrowSelectSmIcon
                                    className={`${blockName}__toggle__arrow`}
                                />
                            ) : (
                                <ArrowSelectIcon
                                    className={`${blockName}__toggle__arrow`}
                                />
                            ))}
                    </span>
                ) : null
            }
        </div>
        /* eslint-enable jsx-a11y/no-static-element-interactions, jsx-a11y/click-events-have-key-events */
    );
};

export default Placeholder;
